html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Rubik", sans-serif;
  font-size: 2rem;
  font-weight: 300;
  background-color: #f6f6f6;
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-thumb {
  background-color: #C9DB4B;
  border-radius: 5px;
}

.contenedor {
  width: 95%;
  max-width: 1300px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  margin: 2rem;
  font-size: 1.8rem;
}

[class$=__btn] {
  background-color: #003E8B;
  padding: 1.2rem 3rem;
  color: white;
  font-size: 1.6rem;
  font-weight: 400;
  display: block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 2.7rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  [class$=__btn] {
    width: auto;
  }
}
[class$=__btn]:hover {
  background-color: #001125;
  color: white;
}

[class$=__separador] {
  border-bottom: 2px dotted rgba(255, 255, 255, 0.514);
  margin-bottom: 1.5rem;
}
@media (min-width: 1024px) {
  [class$=__separador] {
    margin: 0 auto;
    display: block;
    width: 71rem;
  }
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
  color: #003E8B;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 3rem;
}
@media (min-width: 1024px) {
  h1 {
    font-size: 4.5rem;
  }
}

h2 {
  font-size: 2.5rem;
}
@media (min-width: 1024px) {
  h2 {
    font-size: 3.4rem;
  }
}

h3 {
  font-size: 2rem;
  font-weight: 300;
}
@media (min-width: 1024px) {
  h3 {
    font-size: 2.4rem;
  }
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #313131;
  font-weight: 400;
}

p {
  font-size: 1.8rem;
  line-height: 1.2;
  color: #313131;
}

.planes {
  margin-top: 5rem;
  padding: 5rem;
}
.planes__etiquetas {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .planes__etiquetas {
    border-bottom: solid 2.5px #C9DB4B;
    flex-direction: row;
    gap: 0;
  }
}
.planes__titulos {
  padding: 1.5rem 5rem;
  border-radius: 10rem;
  border: solid 1px #003E8B;
}
@media (min-width: 1024px) {
  .planes__titulos {
    border: none;
    border-radius: 2rem 2rem 0 0;
    margin-bottom: 0;
  }
  .planes__titulos:hover {
    opacity: 85%;
  }
}
.planes__titulo {
  font-size: 2.6rem;
  font-weight: 700;
}

.swicth {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
}
.swicth__container {
  background-color: #f1f1f1;
  padding-top: 0.4rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  width: 12rem;
  border-radius: 10rem;
  box-shadow: inset 2px 2px 5px 2px rgba(0, 0, 0, 0.123);
}
.swicth__link {
  display: flex;
  justify-content: right;
}
.swicth__texto {
  font-weight: 400;
}

.tarjetas {
  margin-top: 1rem;
  padding: 1rem 0;
}
.tarjetas p {
  font-weight: 700;
}
@media (min-width: 768px) {
  .tarjetas {
    padding: 3rem 0;
  }
}
@media (min-width: 1024px) {
  .tarjetas {
    display: grid;
    gap: 1.5rem;
    grid-template-columns: repeat(3, 1fr);
    justify-content: center;
  }
}
.tarjetas__contenedor {
  text-align: center;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.tarjetas__subtitulo {
  margin-bottom: 5rem;
  font-size: 2.2rem;
  font-weight: 700;
}
.tarjetas__subtitulo--basico {
  margin-bottom: 1rem;
  font-size: 3rem;
  font-weight: 700;
}
.tarjetas__texto {
  margin-top: 0;
  font-weight: 300;
}
.tarjetas__contenido {
  background-color: #f1f1f1;
  padding: 1.5rem 3rem;
  border-radius: 2rem;
  box-shadow: 2px 2px 7px 2px rgba(0, 0, 0, 0.123);
  margin-bottom: 2rem;
  width: 35rem;
}
.tarjetas__usuariosInfo {
  border-bottom: 1px solid #e2e2e2;
}
.tarjetas__precio {
  font-weight: 700;
  font-size: 3rem;
}
.tarjetas__precio::before {
  content: "$ ";
}
.tarjetas__precio--descuento {
  font-weight: 700;
  font-size: 3rem;
}
.tarjetas__precio--descuento::before {
  content: "$ ";
}
.tarjetas__precio--del {
  color: #707070;
  font-weight: 300;
}
.tarjetas__servicio {
  border-bottom: 1px solid #e2e2e2;
  padding-bottom: 1.2rem;
}
.tarjetas__servicio::before {
  content: "";
  background-image: url(../img/Icon-check-circle.svg);
  height: 2rem;
  width: 2rem;
  margin-right: 1rem;
  display: inline-block;
  background-repeat: no-repeat;
  background-position: bottom center;
}
.tarjetas__usuario {
  font-weight: 700;
  font-size: 2.3rem;
  margin-bottom: 0;
}
.tarjetas__usuarios {
  margin-bottom: 1rem;
  display: grid;
  gap: 0;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .tarjetas__usuarios {
    flex-direction: row;
    gap: 1rem;
  }
}
.tarjetas__usuarios p {
  margin: 1rem 0 0 0;
  font-weight: 300;
  font-size: 1.5rem;
}
.tarjetas__plan {
  background-color: #f1f1f1;
  padding: 1.5rem 3rem;
  border-radius: 2rem;
  box-shadow: 2px 2px 7px 2px rgba(0, 0, 0, 0.123);
  margin-bottom: 2rem;
  width: 35rem;
}
.tarjetas__plan--personalizado {
  background-color: #003E8B;
  padding: 4.3rem 3rem;
  border-radius: 2rem;
  box-shadow: 2px 2px 7px 2px rgba(0, 0, 0, 0.123);
  margin-bottom: 2rem;
  width: 35rem;
  margin-top: 0;
}
.tarjetas__subtitulo--personalizado {
  margin-bottom: 1rem;
  font-weight: 700;
  font-size: 2.5rem;
}
.tarjetas__precio--personalizado {
  margin-top: 0;
  color: white;
  font-size: 2.5rem;
}
.tarjetas__boton--personalizado {
  background-color: #C9DB4B;
  padding: 1.2rem 3rem;
  color: #003E8B;
  font-size: 1.6rem;
  font-weight: 400;
  display: block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 2.7rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .tarjetas__boton--personalizado {
    width: auto;
  }
}
.tarjetas__boton--personalizado:hover {
  background-color: #90a020;
  color: white;
}
.tarjetas__boton--personalizadoDos {
  margin-top: 2rem;
  background-color: rgba(100, 161, 241, 0.493);
  padding: 1.2rem 3rem;
  color: white;
  font-size: 1.6rem;
  font-weight: 400;
  display: block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 2.7rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .tarjetas__boton--personalizadoDos {
    width: auto;
  }
}
.tarjetas__boton--personalizadoDos:hover {
  background-color: rgba(20, 106, 219, 0.493);
  color: white;
}
.tarjetas__encabezado {
  margin-top: 0;
}
.tarjetas__totalPago {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tarjetas__texto--personalizado {
  color: white;
  font-size: 1.2rem;
}
.tarjetas__boton {
  background-color: #707070;
  padding: 1.2rem 3rem;
  color: white;
  font-size: 1.6rem;
  font-weight: 400;
  display: block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 2.7rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .tarjetas__boton {
    width: auto;
  }
}
.tarjetas__boton:hover {
  background-color: #3d3d3d;
  color: white;
}

.separador {
  border-bottom: 2px solid #003E8B;
  margin-bottom: 1rem;
  margin-top: 1rem;
  width: 100%;
}

.ocultarPlan {
  display: none;
}

.activoBg-titulo {
  background-color: #C9DB4B;
}

.activoBtn {
  background-color: #C9DB4B;
  padding: 1.2rem 3rem;
  color: #003E8B;
  font-size: 1.6rem;
  font-weight: 400;
  display: block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 2.7rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .activoBtn {
    width: auto;
  }
}
.activoBtn:hover {
  background-color: #90a020;
  color: white;
}

.swicthActivo {
  justify-content: start;
}